body {
  --link-color: #00acdc;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  width: 100%;
}

.wrapper {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  align-items: center;
  margin-left: 50px;
  display: flex;
}

.nav_menu {
  margin: 0 50px 0 0;
  padding: 0;
  display: flex;
}

.nav_menu_item {
  height: 60px;
  list-style: none;
}

.nav_menu_link {
  color: #000;
  text-transform: uppercase;
  height: 100%;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav_menu_link:after {
  content: "";
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

a[data-qa="hover"]:hover {
  color: var(--link-color);
}

.nav_menu_item:nth-child(n+2) {
  margin-left: 20px;
}

.nav_menu_link.is-active {
  color: var(--link-color);
}

.nav_menu_link.is-active:after {
  background-color: var(--link-color);
}

